/*------------------------------------
	General
------------------------------------*/
* {
  box-sizing: border-box;
  outline: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.wrapper {
  font-size: 0.9375rem;
}

main {
  position: relative;
}

a:hover {
  text-decoration: none;
}

a:focus {
  text-decoration: none;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  line-height: 1.5rem;
  font-weight: 500;
}

.heading-letter-spacing {
  letter-spacing: 1px;
}

figure {
  margin-bottom: 0;
}

object {
  margin-bottom: -0.3125rem;
}

.register-mobile {
  text-align: center;
}

.register-mobile li {
  font-weight: 700;
  display: inline;
}

.cursors {
  cursor: pointer;
}

pre {
  overflow: auto;
  background-color: var(--bs-gray-100);
  padding: 4px 10px;
  border: none;
  border-radius: 0;
  margin-top: 24px;
}

ol ul {
  margin-left: 1rem;
  margin-bottom: 1rem;
}

.tags-list, .comment-list {
  padding-left: 0;
}

:focus {
  outline: 0;
}

a:focus {
  outline: 0;
}

button:focus {
  outline: 0;
}

.hr-after.mx-auto {
  opacity: 1;
  height: 2px;
}

/*------------------------------------
	Color
------------------------------------*/
a {
  color: var(--bs-link-color);
