	Vertical slider
------------------------------------*/
.top-indicator {
  right: 0;
  top: 1rem;
  bottom: inherit;
  left: inherit;
  justify-content: right;
  margin-right: 1rem;
}

.slider-caption {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  color: var(--bs-white);
  background-color: rgba(27, 27, 27, 0.5);
  text-align: left;
  font-size: 1rem;
  padding: 1.333rem;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

/*------------------------------------
	Responsive maps
------------------------------------*/
.responsive-maps {
  position: relative;
  padding-bottom: 75%;
  overflow: hidden;
}

.responsive-maps iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}

